import { openMenuPanel, closeMenuPanel } from "./menu";
import Preloader from "./preloader";
const eventCard = document.querySelectorAll(".event__carousel__slide");
const eventPopup = document.querySelector(".event__popup");
const popupBackground = document.querySelector(".popup__background");
const popupClose = document.querySelectorAll(".popup__container__close");

// eventCard.addEventListener("click", (e) => {
//   const clicked = e.target.closest(".event__carousel__slide");
//   console.log(clicked);
// });

eventCard.forEach((card) => {
  card.addEventListener("click", (e) => {
    const clicked = e.target.closest(".event__carousel__slide");
    const container = document.querySelector(
      `.popup__container--${clicked.dataset.card}`
    );
    container.classList.remove("hidden");
    popupBackground.classList.remove("hidden");

    console.log(container);
  });
});

popupClose.forEach((button) => {
  button.addEventListener("click", (e) => {
    const clicked = e.target.closest(".popup__container");
    clicked.classList.add("hidden");
    popupBackground.classList.add("hidden");
  });
});

//Function to open/close menu
openMenuPanel();
closeMenuPanel();

